
.taskList{
    display: flex;

    flex-direction: column-reverse;
}
.taskItemWrapper{
    border-radius: 5px;
    padding: 15px 15px 15px 15px;
    max-width: 490px;
    width: 490px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #F1EFE1;
    margin-bottom: 32px;

}
.taskCheckBox{
    width: 30px;
    height: 30px;
    background: #F1EFE1;
    margin-right: 25px;
}
.taskBody{

}

.taskItemControl{
    display: flex;
    gap: 10px;
}
.deleteTask{
    position: relative;
    bottom:5px;
    height: 25px;
    width:  25px;
    background: url('../../img/delete.svg')0px 0 /100% 100%  no-repeat;
}
.editTask{
    height: 20px;
    width:  20px;;
    background: url('../../img/edit.svg')  no-repeat;;
}
